// 
const myRequest = (options = {
	hasLoading: true,
	url: '',
	method: 'GET',
	data: {}
}) => {
	//  loading
	if (options.hasLoading) {
		uni.showLoading({
			title: '...'
		});
	}
	return new Promise((resolve, reject) => {
		// console.log(options)
		uni.request({
			url: options.url,
			method: options.method,
			data: options.data,
			header: {
				'content-type': 'application/json; charset=utf-8'
			},
			// 
			xhrFields: {
				withCredentials: true
			},
			success: (res) => {
				//  loading
				if (options.hasLoading) {
					uni.hideLoading();
				}
				// console.log(res)
				if (res.data.code == 0) {
					return uni.showToast({
						title: '',
						// false
						mask: true,
						// 1500
						duration: 1500
					})
				}
				resolve(res);
			},
			fail: (err) => {
				if (options.hasLoading) {
					uni.hideLoading();
				}
				// console.log(err)
				uni.showToast({
					title: '',
					mask: true
				})
				reject(err)
			}
		})
	})
}

export default {
	myRequest
}

